import { faCalendarDay, faUsers } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React, { useState } from "react";
import { Nav, NavItem, NavLink, TabContent, TabPane } from "reactstrap";
import Main from "../../Dashboard/Main/Main";
import Schedules from "../../Dashboard/Schedules/Schedules";
import "./dashboardtab.css";

export default function DashboardTab(props) {
  const [activeTab, setActiveTab] = useState("1");

  const handleTabChange = (value) => {
    setActiveTab(value);
  };

  return (
    <div className="mt-5 pt-0 mt-0 dashboardTab">
      <Nav tabs>
        <NavItem>
          <NavLink
            className={
              activeTab === "1" ? "active activeTabHeader" : "inactiveTab"
            }
            onClick={() => handleTabChange("1")}
          >
            <FontAwesomeIcon icon={faUsers} /> Passengers
          </NavLink>
        </NavItem>
        <NavItem>
          <NavLink
            className={
              activeTab === "2" ? "active activeTabHeader" : "inactiveTab"
            }
            onClick={() => handleTabChange("2")}
          >
            <FontAwesomeIcon icon={faCalendarDay} /> Schedules
          </NavLink>
        </NavItem>
      </Nav>
      <TabContent activeTab={activeTab} className="bg-white pl-2 pr-2 pb-2">
        <TabPane tabId="1">
          <Main />
        </TabPane>
        <TabPane tabId="2">{activeTab == "2" && <Schedules />}</TabPane>
      </TabContent>
    </div>
  );
}
