import React, { useEffect, useState } from "react";
import API from "../../../Api";
import Swal from "sweetalert2";

import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Row,
  Col,
  Card,
  CardBody,
} from "reactstrap";

import { TextField } from "@material-ui/core";
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faSave,
  faShip,
  faWindowClose,
} from "@fortawesome/free-solid-svg-icons";
import { useAuth } from "../../../context/auth";
import TransportType from "../../UI/Forms/Fields/TransportType";
import { useShip } from "../../../context/ship";

const UpdateShip = (props) => {
  const authToken = useAuth().authTokens.access_token;
  const ship_context = useShip();
  const selectedShip = useShip().selectedShip;
  const [isProcessing, setIsProcessing] = useState(false);
  const [transportID, setTransportID] = useState(null);
  const [shipName, setShipName] = useState(null);
  const [capacity, setCapacity] = useState(0);
  const [voyageRoute, setVoyageRoute] = useState(null);
  const [transportType, setTransportType] = useState(null);

  const saveNewRecordHandler = (e) => {
    e.preventDefault();
    setIsProcessing(true);
    let token = authToken;
    let postData = {
      name: shipName,
      capacity: capacity,
      routes: voyageRoute,
      transportation_category_id: transportType,
    };

    API.put(`carrier/transport/update/${transportID}`, postData, {
      headers: {
        Authorization: "bearer " + token,
      },
    })
      .then((result) => {
        if (result.data.status) {
          setIsProcessing(false);

          Swal.fire(
            "Transaction Completed!",
            "Data Successfully Created!",
            "success"
          );

          ship_context.loadShips();

          props.toggle();
        } else {
          setIsProcessing(false);
          Swal.fire("Transaction Failed!", result.data.message, "error");
        }
      })
      .catch((e) => {
        setIsProcessing(false);

        Swal.fire("Transaction Failed!", "error");
      });
  };

  useEffect(() => {
    setTransportID(selectedShip[0].id);
    setShipName(selectedShip[0].name);
    setCapacity(selectedShip[0].capacity);
    setVoyageRoute(selectedShip[0].routes);
    setTransportType(selectedShip[0].transportation_category_id);
    return () => {};
  }, [selectedShip]);

  return (
    <div>
      <Modal
        backdrop={"static"}
        isOpen={props.isOpen}
        toggle={props.toggle}
        className="modal-md"
        style={{ marginTop: "10%" }}
      >
        <ModalHeader toggle={props.toggle}>
          <FontAwesomeIcon icon={faShip} /> Update Ship
        </ModalHeader>
        <form onSubmit={(e) => saveNewRecordHandler(e)}>
          <ModalBody>
            <Row className="p-2">
              <Col md={12}>
                <Card>
                  <CardBody>
                    <TextField
                      className="m-0"
                      value={shipName || ""}
                      onChange={(e) => setShipName(e.target.value)}
                      name="shipName"
                      label="Ship Name"
                      required
                      fullWidth
                    />
                    <TextField
                      type="number"
                      InputProps={{ inputProps: { min: 0 } }}
                      value={capacity || ""}
                      className="m-0"
                      onChange={(e) => setCapacity(e.target.value)}
                      name="capacity"
                      label="Capacity"
                      required
                      fullWidth
                    />
                    <TextField
                      className="m-0 mt-1 mb-1"
                      value={voyageRoute || ""}
                      onChange={(e) => setVoyageRoute(e.target.value)}
                      name="voyageRoute"
                      label="Route"
                      required
                      fullWidth
                    />
                    <TransportType
                      transportType={transportType}
                      setTransportType={setTransportType}
                    />
                  </CardBody>
                </Card>
              </Col>
            </Row>
            <div>
              {isProcessing && (
                <Row className="p-2">
                  <Col className="text-center">
                    <LinearProgressBarWithLabel title="Updating Ship Record" />
                  </Col>
                </Row>
              )}
            </div>
          </ModalBody>
          <ModalFooter>
            <Button
              type="submit"
              color="primary"
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faSave} /> Save
            </Button>{" "}
            <Button
              color="danger"
              onClick={props.toggle}
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faWindowClose} /> Cancel
            </Button>
          </ModalFooter>
        </form>
      </Modal>
    </div>
  );
};

export default UpdateShip;
