import React, { useState, useEffect, useCallback } from "react";
import { useAuth } from "../../../context/auth";
import { useTheme } from "../../../context/theme";
import { ProfileContext } from "../../../context/profile";
import { ShipContext } from "../../../context/ship";
import API from "../../../Api";
import { debounce, isArray } from "lodash";
import { Tooltip } from "@material-ui/core";
import { Container, Button, Row, Col, Card, CardBody } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faShip, faUser } from "@fortawesome/free-solid-svg-icons";
import DataTable from "../../UI/Datatable/Datatable";
import NewPassengerManifestsModal from "../../UI/Modals/NewPassengerManifests";
import NewShip from "../../UI/Modals/NewShip";
import PassengerProfile from "../../UI/Modals/PassengerProfile";
import TransportDetails from "../../UI/Modals/TransportDetails";
import NewPassenger from "../../UI/Modals/NewPassenger";
import MenuButton from "../../UI/Common/OptionMenuButton";
import FilterMenuButton from "../../UI/Common/FilterMenuButton";

const Main = (props) => {
  const token = useAuth().authTokens;
  const modalMargin = useTheme().modalMargin;
  const [passengerData, setPassengerData] = useState([]);
  const [currentPage, setCurrentPage] = useState();
  const [selectedProfile, setSelectedProfile] = useState({});
  const [isLoading, setIsLoading] = useState(false);
  const [shipOptions, setShipOptions] = useState([]);
  const [transportOptions, setTransportOptions] = useState([]);

  // Modals
  const [newManifestModal, setNewManifestModal] = useState(false);
  const toggleNewManifestModal = () => setNewManifestModal(!newManifestModal);
  const [newPassenger, setNewPassenger] = useState(false);
  const toggleNewPassengerModal = () => setNewPassenger(!newPassenger);
  const [passengerProfile, setPassengerProfile] = useState(false);
  const togglePassengerProfileModal = () =>
    setPassengerProfile(!passengerProfile);
  const [transportDetails, setTransportDetails] = useState(false);
  const toggleTransportDetails = () => setTransportDetails(!transportDetails);
  const [newShip, setNewShip] = useState(false);
  const toggleNewShip = () => setNewShip(!newShip);

  const formatData = (data = null) => {
    if (data === null) {
      return null;
    }

    if (isArray(data)) {
      return formatArrayData(data);
    }

    return formatObjectData(data);
  };

  const formatObjectData = (data) => {
    let person = [];
    let index = 0;

    for (const [key, value] of Object.entries(data)) {
      let profile;
      let rawData = value;

      profile = rawData.profile;

      person[index] = [
        profile.id,
        `${profile.first_name.toUpperCase()} ${profile.last_name.toUpperCase()}`,
        profile.age,
        profile.gender.toUpperCase() === "F" ? "FEMALE" : "MALE",
        profile.email,
        profile.mobile === null ? "N/A" : profile.mobile,
        [index, rawData, profile],
      ];

      index++;
    }

    return person;
  };

  const formatArrayData = (data) => {
    if (data.length > 0) {
      let person = [];

      return data.map((value, index) => {
        let profile;
        let rawData = value;

        profile = rawData.profile;

        person = [
          profile.id,
          `${profile.first_name.toUpperCase()} ${profile.last_name.toUpperCase()}`,
          profile.age,
          profile.gender.toUpperCase() === "F" ? "FEMALE" : "MALE",
          profile.email,
          profile.mobile === null ? "N/A" : profile.mobile,
          [index, rawData, profile],
        ];

        return person;
      });
    }
  };

  const loadPassengers = useCallback(() => {
    setIsLoading(true);
    API.get("carrier/trace-profiles", {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    })
      .then((result) => {
        let data = result.data.data;
        let currentPage = result.data.current_page;

        if (Object.keys(data).length > 0) {
          let passengers = formatData(data);
          setCurrentPage(currentPage);
          setPassengerData(passengers);
        }

        setIsLoading(false);
      })
      .catch((e) => {
        setIsLoading(false);
      });
  }, []);

  const loadShips = useCallback(() => {
    API.get("carrier/transport/ships", {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    }).then((result) => {
      let ships = [];

      if (result.data.length > 0) {
        for (let s in result.data) {
          let tempShipArr = {
            value: result.data[s].id,
            label: result.data[s].name,
          };
          ships.push(tempShipArr);
        }
      }

      setShipOptions(ships);
    });
  }, []);

  const loadTransportType = useCallback(() => {
    API.get("carrier/transport/category", {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    }).then((result) => {
      let category = [];

      if (result.data.length > 0) {
        for (let s in result.data) {
          let tempCategory = {
            value: result.data[s].id,
            label: result.data[s].name,
          };
          category.push(tempCategory);
        }
      }

      setTransportOptions(category);
    });
  }, []);

  const appendProfile = (page) => {
    API.get(`carrier/trace-profiles?page=${page}`, {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    }).then((result) => {
      let data = result.data.data;

      let newPassengerProfiles = [];
      let passengers = formatData(data);

      if (result.data.current_page > currentPage) {
        setCurrentPage(result.data.current_page);
        if (passengers !== undefined) {
          newPassengerProfiles = [...passengerData, ...passengers];
        } else {
          newPassengerProfiles = passengerData;
        }
      }

      setPassengerData(newPassengerProfiles);
    });
  };

  const debounceSearchHandler = debounce(
    (queryData) => searchProfile(queryData),
    500
  );

  const searchProfile = (query) => {
    return API.post(
      `carrier/search`,
      {
        _text: query,
      },
      {
        headers: {
          Authorization: "bearer " + token.access_token,
        },
      }
    ).then((result) => {
      let data = result.data;
      let passengers = formatData(data);
      setPassengerData(passengers);
      setIsLoading(false);
    });
  };

  const handlePassengerProfileDetails = (data) => {
    setSelectedProfile(data);
    togglePassengerProfileModal();
  };

  const handlePassengerTransportDetails = (data) => {
    setSelectedProfile(data);
    toggleTransportDetails();
  };

  const columns = [
    "Ref #",
    "Name",
    "Age",
    "Gender",
    "Email",
    "Contact",
    {
      name: "Actions",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div style={{ color: "#1a1751" }}>
              <Tooltip title="Profile Details" placement="top">
                <Button
                  color="link"
                  className="p-1 m-0 btn"
                  onClick={() => handlePassengerProfileDetails(value)}
                >
                  <FontAwesomeIcon icon={faUser} />
                </Button>
              </Tooltip>
              <Tooltip title="Transport Details" placement="top">
                <Button
                  color="link"
                  className="p-1 m-0 btn text-primary"
                  onClick={() => handlePassengerTransportDetails(value)}
                >
                  <FontAwesomeIcon icon={faShip} />
                </Button>
              </Tooltip>
            </div>
          );
        },
      },
    },
  ];

  const options = {
    fixHeader: true,
    filterType: "dropdown",
    responsive: "vertical",
    tableBodyHeight: "350px",
    selectableRows: "none",
    filter: false,
    viewColumns: false,
    elevation: 0,
    print: false,
    download: false,
    rowsPerPage: 5,
    rowsPerPageOptions: [5, 30, 100],
    onChangePage: (p) => {
      if (p % 2 !== 0) {
        if (p >= currentPage) {
          appendProfile(currentPage + 1);
        }
      }
    },
    onSearchClose: () => {
      setIsLoading(false);
      loadPassengers();
    },
    searchProps: {
      onKeyUp: (e) => {
        let queryString = e.target.value;
        if (queryString.length >= 2) {
          setIsLoading(true);
          debounceSearchHandler(queryString);
        }
      },
    },
    // this is to disable auto search on local data
    customSearch: (query, row, col) => {
      return passengerData.length > 0;
    },
  };

  useEffect(() => {
    loadPassengers();
    loadShips();
    loadTransportType();
    return () => {};
  }, [loadPassengers, loadShips, loadTransportType]);

  return (
    <ProfileContext.Provider
      value={{ passengerData, loadPassengers, selectedProfile }}
    >
      <ShipContext.Provider
        value={{ shipOptions, loadShips, transportOptions }}
      >
        <Container fluid="true" className="mt-0 p-0 m-0 ">
          <Row className="m-0 p-0 text-right">
            <Col md={12}>
              <Row>
                <Col md={12} className="m-0 p-0">                  
                  <MenuButton
                    toggleNewManifestModal={toggleNewManifestModal}
                    toggleNewPassengerModal={toggleNewPassengerModal}
                    toggleNewShip={toggleNewShip}
                  />
                  <FilterMenuButton />
                </Col>
              </Row>
            </Col>
            <Col md={12} className="pl-0 ml-0 pr-0 mr-0 text-left">
              <DataTable
                title="Passengers"
                loadPassengers={loadPassengers}
                data={passengerData}
                isLoading={isLoading}
                columns={columns}
                options={options}
              />
            </Col>
          </Row>
        </Container>
        <NewPassengerManifestsModal
          isOpen={newManifestModal}
          loadPassengers={loadPassengers}
          toggle={toggleNewManifestModal}
        />
        {passengerProfile && (
          <PassengerProfile
            isOpen={passengerProfile}
            toggle={togglePassengerProfileModal}
            modalMargin={modalMargin}
          />
        )}
        {transportDetails && (
          <TransportDetails
            isOpen={transportDetails}
            toggle={toggleTransportDetails}
            modalMargin={modalMargin}
          />
        )}
        {newPassenger && (
          <NewPassenger
            isOpen={newPassenger}
            toggle={toggleNewPassengerModal}
            modalMargin={modalMargin}
            loadPassengers={loadPassengers}
          />
        )}
        {newShip && <NewShip isOpen={newShip} toggle={toggleNewShip} />}
      </ShipContext.Provider>
    </ProfileContext.Provider>
  );
};

export default Main;
