import React, { useState, useEffect, useCallback } from "react";
import { useAuth } from "../../../context/auth";
import { useTheme } from "../../../context/theme";
import API from "../../../Api";
import { ScheduleContext } from "../../../context/schedules";
import { debounce, isArray } from "lodash";
import { Tooltip } from "@material-ui/core";
import { Container, Button, Row, Col, Card, CardBody } from "reactstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faShip, faUser } from "@fortawesome/free-solid-svg-icons";
import DataTable from "../../UI/Datatable/Datatable";
import MenuButton from "../../UI/Common/OptionMenuSchedulesButton";
import FilterMenuButton from "../../UI/Common/FilterMenuButton";
import NewSchedules from "../../UI/Modals/NewSchedules";

const Schedules = (props) => {
  const token = useAuth().authTokens;
  const modalMargin = useTheme().modalMargin;
  const [scheduleData, setscheduleData] = useState([]);
  const [currentPage, setCurrentPage] = useState();
  const [isLoading, setIsLoading] = useState(false);
  const [shipOptions, setShipOptions] = useState([]);

  // Modals
  const [newScheduleModal, setNewScheduleModal] = useState(false);
  const toggleNewScheduleModal = () => setNewScheduleModal(!newScheduleModal);
  const [updateScheduleModal, setUpdateScheduleModal] = useState(false);
  const toggleUpdateScheduleModal = () =>
    setUpdateScheduleModal(!updateScheduleModal);

  const formatData = (data = null) => {
    if (data === null) {
      return null;
    }

    if (isArray(data)) {
      return formatArrayData(data);
    }

    return formatObjectData(data);
  };

  const formatObjectData = (data) => {
    let person = [];
    let index = 0;

    for (const [key, value] of Object.entries(data)) {
      let profile;
      let rawData = value;

      profile = rawData.profile;

      person[index] = [
        profile.id,
        `${profile.first_name.toUpperCase()} ${profile.last_name.toUpperCase()}`,
        profile.age,
        profile.gender.toUpperCase() === "F" ? "FEMALE" : "MALE",
        profile.email,
        profile.mobile === null ? "N/A" : profile.mobile,
        [index, rawData, profile],
      ];

      index++;
    }

    return person;
  };

  const formatArrayData = (data) => {
    if (data.length > 0) {
      let person = [];

      return data.map((value, index) => {
        let profile;
        let rawData = value;

        profile = rawData.profile;

        person = [
          profile.id,
          `${profile.first_name.toUpperCase()} ${profile.last_name.toUpperCase()}`,
          profile.age,
          profile.gender.toUpperCase() === "F" ? "FEMALE" : "MALE",
          profile.email,
          profile.mobile === null ? "N/A" : profile.mobile,
          [index, rawData, profile],
        ];

        return person;
      });
    }
  };

  const loadSchedules = useCallback(() => {
    setIsLoading(true);
    API.get("carrier/transport/schedules", {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    })
      .then((result) => {
        // console.log(result);
        // let data = result.data.data;
        // let currentPage = result.data.current_page;

        // if (Object.keys(data).length > 0) {
        //   let schedules = formatData(data);
        //   setCurrentPage(currentPage);
        //   setscheduleData(schedules);
        // }

        setIsLoading(false);
      })
      .catch((e) => {
        setIsLoading(false);
      });
  }, []);

  const loadShips = useCallback(() => {
    API.get("carrier/transport/ships", {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    }).then((result) => {
      let ships = [];

      if (result.data.length > 0) {
        for (let s in result.data) {
          let tempShipArr = {
            value: result.data[s].id,
            label: result.data[s].name,
          };
          ships.push(tempShipArr);
        }
      }

      setShipOptions(ships);
    });
  }, []);

  const appendSchedules = (page) => {
    API.get(`carrier/trace-profiles?page=${page}`, {
      headers: {
        Authorization: "bearer " + token.access_token,
      },
    }).then((result) => {
      let data = result.data.data;

      let newPassengerProfiles = [];
      let schedules = formatData(data);

      if (result.data.current_page > currentPage) {
        setCurrentPage(result.data.current_page);
        if (schedules !== undefined) {
          newPassengerProfiles = [...scheduleData, ...schedules];
        } else {
          newPassengerProfiles = scheduleData;
        }
      }

      setscheduleData(newPassengerProfiles);
    });
  };

  const debounceSearchHandler = debounce(
    (queryData) => searchSchedules(queryData),
    500
  );

  const searchSchedules = (query) => {
    return API.post(
      `carrier/search`,
      {
        _text: query,
      },
      {
        headers: {
          Authorization: "bearer " + token.access_token,
        },
      }
    ).then((result) => {
      let data = result.data;
      let schedules = formatData(data);
      setscheduleData(schedules);
      setIsLoading(false);
    });
  };

  const handleScheduleDetails = (data) => {
    toggleUpdateScheduleModal();
  };

  const columns = [
    "ID",
    "Transport Name",
    "Origin",
    "Destination",
    "ETA",
    "ETD",
    {
      name: "Actions",
      options: {
        customBodyRender: (value, tableMeta, updateValue) => {
          return (
            <div style={{ color: "#1a1751" }}>
              <Tooltip title="Profile Details" placement="top">
                <Button
                  color="link"
                  className="p-1 m-0 btn"
                  onClick={() => console.log(value)}
                >
                  <FontAwesomeIcon icon={faUser} />
                </Button>
              </Tooltip>
              <Tooltip title="Transport Details" placement="top">
                <Button
                  color="link"
                  className="p-1 m-0 btn text-primary"
                  onClick={() => console.log(value)}
                >
                  <FontAwesomeIcon icon={faShip} />
                </Button>
              </Tooltip>
            </div>
          );
        },
      },
    },
  ];

  const options = {
    fixHeader: true,
    filterType: "dropdown",
    responsive: "vertical",
    tableBodyHeight: "350px",
    selectableRows: "none",
    filter: false,
    viewColumns: false,
    elevation: 0,
    print: false,
    download: false,
    rowsPerPage: 5,
    rowsPerPageOptions: [5, 30, 100],
    onChangePage: (p) => {
      if (p % 2 !== 0) {
        if (p >= currentPage) {
          appendSchedules(currentPage + 1);
        }
      }
    },
    onSearchClose: () => {
      setIsLoading(false);
      loadSchedules();
    },
    searchProps: {
      onKeyUp: (e) => {
        let queryString = e.target.value;
        if (queryString.length >= 2) {
          setIsLoading(true);
          debounceSearchHandler(queryString);
        }
      },
    },
    // this is to disable auto search on local data
    customSearch: (query, row, col) => {
      return scheduleData.length > 0;
    },
  };

  useEffect(() => {
    loadSchedules();
    loadShips();
    return () => {};
  }, [loadSchedules, loadShips]);

  return (
    <ScheduleContext.Provider value={{ loadSchedules, shipOptions }}>
      <Container fluid="true" className="mt-0 p-0 m-0 ">
        <Row className="m-0 p-0 text-right">
          <Col md={12}>
            <Row>
              <Col md={12} className="m-0 p-0">
                <MenuButton toggleNewScheduleModal={toggleNewScheduleModal} />
                <FilterMenuButton />
              </Col>
            </Row>
          </Col>
          <Col md={12} className="pl-0 ml-0 pr-0 mr-0 text-left">
            <DataTable
              title="Schedules"
              loadSchedules={loadSchedules}
              data={scheduleData}
              isLoading={isLoading}
              columns={columns}
              options={options}
            />
          </Col>
        </Row>
      </Container>
      {newScheduleModal && (
        <NewSchedules
          isOpen={newScheduleModal}
          toggle={toggleNewScheduleModal}
        />
      )}
    </ScheduleContext.Provider>
  );
};

export default Schedules;
