import React, { useState } from "react";
import { Switch, Route } from "react-router-dom";
import { useAuth } from "../../context/auth";
import { ThemeContext } from "../../context/theme";
import Icon from "../../assets/icon.png";

import Footer from "./Footer/Footer";
import ChangePasswordModal from "../UI/Modals/ChangePassword";
import Report from "../UI/Modals/Report";

import {
  Drawer,
  AppBar,
  Toolbar,
  List,
  Typography,
  IconButton,
  ListItem,
  ListItemIcon,
  ListItemText,
  Collapse,
} from "@material-ui/core";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCog, faPrint, faShip } from "@fortawesome/free-solid-svg-icons";

import MenuIcon from "@material-ui/icons/Menu";
import AccountCircleIcon from "@material-ui/icons/AccountCircle";
import HomeIcon from "@material-ui/icons/Home";
import ExitToAppIcon from "@material-ui/icons/ExitToApp";
import ExpandMoreIcon from "@material-ui/icons/ExpandMore";
import ExpandLessIcon from "@material-ui/icons/ExpandLess";
import AccountBoxIcon from "@material-ui/icons/AccountBox";

import clsx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import "./dashboard.css";
import Ships from "./Ships/Ships";
import DashboardTab from "../UI/Tabs/DashboardTab";

const drawerWidth = 220;

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
  },
  appBar: {
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    // backgroundColor: '#F8B828'
    backgroundColor: "#1a1751",
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(["margin", "width"], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  hide: {
    display: "none",
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
    boxShadow: "1px 0px 10px #000",
  },
  drawerHeader: {
    display: "flex",
    // height: '170px',
    alignItems: "center",
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    // justifyContent: 'flex-end',
    backgroundColor: "#FFFFFF",
    color: "#FFFFFF",
    margin: "0px",
    padding: "0px",
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(4),
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
  large: {
    width: theme.spacing(10),
    height: theme.spacing(10),
  },
  nested: {
    paddingLeft: theme.spacing(4),
  },
}));

export default function Dashboard(props) {
  const token = useAuth().authTokens;
  const classes = useStyles();
  const [drawerStatus, setDrawerStatus] = useState(true);
  const [isManageCollapse, setIsManageCollapse] = useState(false);
  const [modalMargin, setModalMargin] = useState("20%");

  const [changePasswordModal, setVisiblechangePasswordModal] = useState(false);
  const toggleChangePasswordModal = () =>
    setVisiblechangePasswordModal(!changePasswordModal);
  const [reportModal, setReportModal] = useState(false);
  const toggleReportModal = () => setReportModal(!reportModal);

  const handleDrawer = () => {
    setDrawerStatus(!drawerStatus);
    if (drawerStatus) {
      setModalMargin("13%");
    } else {
      setModalMargin("20%");
    }
  };

  const handleManageCollapse = () => {
    setIsManageCollapse(!isManageCollapse);
  };

  const handleNavigation = (stringPath = "/") => {
    switch (stringPath) {
      case "/":
        props.history.push("/");
        break;
      case "ships":
        props.history.push("/ships");
        break;
      default:
        break;
    }
  };

  const handleLogout = () => {
    sessionStorage.removeItem("transporttokens");
    window.location.reload();
  };

  const changePasswordHandler = (e) => {
    e.preventDefault();
    toggleChangePasswordModal();
  };

  const reportHandler = () => {
    toggleReportModal();
  };

  return (
    <ThemeContext.Provider value={{ modalMargin }}>
      <div className={classes.root}>
        <AppBar
          position="fixed"
          className={clsx(classes.appBar, {
            [classes.appBarShift]: drawerStatus,
          })}
        >
          <Toolbar>
            <IconButton
              color="inherit"
              aria-label="open drawer"
              onClick={handleDrawer}
              edge="start"
              className={clsx(classes.menuButton)}
              style={{ outline: "none" }}
            >
              <MenuIcon />
            </IconButton>
            <Typography variant="h6" noWrap>
              <div className="float-left">
                <img src={Icon} width="33" height="35" alt="EMS Logo" />
              </div>
              <div className="float-left">
                <ul
                  className="p-0 m-0 mt-2 ml-1"
                  style={{ listStyle: "none", lineHeight: "0.9rem" }}
                >
                  <li style={{ fontSize: "1.5rem" }}>PEMS</li>
                  <li>
                    <small style={{ fontSize: "0.8rem" }}>
                      Passenger e-Manifest System
                    </small>
                  </li>
                </ul>
              </div>
            </Typography>
          </Toolbar>
        </AppBar>
        <Drawer
          className={classes.drawer}
          variant="persistent"
          anchor="left"
          open={drawerStatus}
          classes={{
            paper: classes.drawerPaper,
          }}
        >
          <div className={classes.drawerHeader}>
            <div
              className="container-fluid text-left mb-2"
              style={{ color: "#1a1751", minHeight: "125px", height: "125px" }}
            >
              <div className="text-center mt-4 pb-2 border-bottom">
                <AccountCircleIcon style={{ width: "45px", height: "50px" }} />
                <ul
                  className="m-0 p-0 mt-2"
                  style={{ listStyle: "none", lineHeight: "1em" }}
                >
                  <li className="border p-1 bg-primary text-white">
                    <strong>{token.user.toUpperCase()}</strong>
                  </li>
                  <li className="mt-2">Operator</li>
                </ul>
              </div>
            </div>
          </div>
          <List style={{ display: "flex", flexDirection: "column" }}>
            <ListItem
              className="mt-4"
              button
              onClick={() => handleNavigation("/")}
            >
              <ListItemIcon>
                <HomeIcon />
              </ListItemIcon>
              <ListItemText primary={"Dashboard"} />
            </ListItem>
            <ListItem button onClick={(e) => changePasswordHandler(e)}>
              <ListItemIcon>
                <AccountBoxIcon />
              </ListItemIcon>
              <ListItemText primary={"My Account"} />
            </ListItem>
            <ListItem button onClick={(e) => reportHandler(e)}>
              <ListItemIcon>
                <FontAwesomeIcon icon={faPrint} />
              </ListItemIcon>
              <ListItemText primary={"Report"} />
            </ListItem>
            <ListItem button onClick={handleManageCollapse}>
              <ListItemIcon>
                <FontAwesomeIcon icon={faCog} size="lg" />
              </ListItemIcon>
              <ListItemText primary="Manage" />
              {isManageCollapse ? <ExpandLessIcon /> : <ExpandMoreIcon />}
            </ListItem>
            <Collapse in={isManageCollapse} timeout="auto" unmountOnExit>
              <List component="div" disablePadding>
                <ListItem
                  button
                  onClick={() => handleNavigation("ships")}
                  className={classes.nested}
                >
                  <ListItemIcon>
                    <FontAwesomeIcon icon={faShip} />
                  </ListItemIcon>
                  <ListItemText primary={"Ships"} />
                </ListItem>
              </List>
            </Collapse>
            {/* <a href={APK} download="PEMS.apk" className="btn m-0 p-0">
                    <ListItem button>
                        <ListItemIcon><AndroidIcon /></ListItemIcon>
                        <ListItemText primary={"Download APK"}></ListItemText>
                    </ListItem>
                    </a>     */}
            <ListItem button onClick={() => handleLogout()}>
              <ListItemIcon>
                <ExitToAppIcon />
              </ListItemIcon>
              <ListItemText primary={"Logout"} />
            </ListItem>
            <Footer />
          </List>
        </Drawer>
        <main
          className={clsx(classes.content, {
            [classes.contentShift]: drawerStatus,
          })}
        >
          <Switch>
            <Route exact path="/">
              <DashboardTab />
            </Route>
            <Route exact path="/ships">
              <Ships />
            </Route>
          </Switch>

          {changePasswordModal && (
            <ChangePasswordModal
              isOpen={changePasswordModal}
              toggle={toggleChangePasswordModal}
            />
          )}

          {reportModal && (
            <Report isOpen={reportModal} toggle={toggleReportModal} />
          )}
        </main>
      </div>
    </ThemeContext.Provider>
  );
}
