import React, { useState } from "react";
import API from "../../../Api";
import { useAuth } from "../../../context/auth";
import { useSchedule } from "../../../context/schedules";
import Swal from "sweetalert2";
import {
  Button,
  Modal,
  ModalHeader,
  ModalBody,
  ModalFooter,
  Row,
  Col,
  Card,
  CardBody,
} from "reactstrap";

import { TextField } from "@material-ui/core";
import LinearProgressBarWithLabel from "../Common/LinearProgressBarWithLabel";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faSave,
  faShip,
  faWindowClose,
} from "@fortawesome/free-solid-svg-icons";
import ShipNameSelect from "../../UI/Forms/Fields/ShipNameSelect";
import ArrivalDate from "../../UI/Forms/Fields/ArrivalDate";
import DepartureDate from "../../UI/Forms/Fields/DepartureDate";

const NewSchedules = (props) => {
  const authToken = useAuth().authTokens.access_token;
  const schedule_context = useSchedule();
  const [isProcessing, setIsProcessing] = useState(false);

  const [ship, setShip] = useState(null);
  const [voyage, setVoyage] = useState(0);
  const [departure, setDeparture] = useState(null);
  const [arrival, setArrival] = useState(null);
  const [fromLocation, setFromLocation] = useState("");
  const [toLocation, setToLocation] = useState("");

  const saveNewRecordHandler = (e) => {
    e.preventDefault();
    setIsProcessing(true);
    let token = authToken;
    let postData = {
      transportation_id: ship,
      voyage: voyage,
      departure: departure,
      arrival: arrival,
      fromLocation: fromLocation,
      toLocation: toLocation
    };

    API.post("store/ships/store", postData, {
      headers: {
        Authorization: "bearer " + token,
      },
    })
      .then((result) => {
        if (result.data.status) {
          setIsProcessing(false);

          Swal.fire(
            "Transaction Completed!",
            "Data Successfully Created!",
            "success"
          );

          schedule_context.loadSchedules();
          props.toggle();

        } else {
          setIsProcessing(false);
          Swal.fire("Transaction Failed!", result.data.message, "error");
        }
      })
      .catch((e) => {
        setIsProcessing(false);
        Swal.fire("Transaction Failed!", "error");
      });
  };

  return (
    <div>
      <Modal
        backdrop={"static"}
        isOpen={props.isOpen}
        toggle={props.toggle}
        className="modal-md"
        style={{ marginTop: "10%" }}
      >
        <ModalHeader toggle={props.toggle}>
          <FontAwesomeIcon icon={faShip} /> New Ship
        </ModalHeader>
        <form onSubmit={(e) => saveNewRecordHandler(e)}>
          <ModalBody>
            <Row className="p-2">
              <Col md={12}>
                <Card>
                  <CardBody>
                    <ShipNameSelect setShipName={setShip} />
                    <TextField                      
                      className="m-0"
                      onChange={(e) => setFromLocation(e.target.value)}
                      name="fromLocation"
                      label="Origin"
                      required
                      fullWidth
                    />
                     <TextField                      
                      className="m-0"
                      onChange={(e) => setToLocation(e.target.value)}
                      name="toLocation"
                      label="Destination"
                      required
                      fullWidth
                    />
                    <TextField
                      className="m-0 mt-1 mb-1"
                      onChange={(e) => setVoyage(e.target.value)}
                      name="voyage"
                      label="Voyage Number"
                      required
                      fullWidth
                    />
                    <ArrivalDate />
                    <DepartureDate />
                  </CardBody>
                </Card>
              </Col>
            </Row>
            <div>
              {isProcessing && (
                <Row className="p-2">
                  <Col className="text-center">
                    <LinearProgressBarWithLabel title="Creating New Ship Record" />
                  </Col>
                </Row>
              )}
            </div>
          </ModalBody>
          <ModalFooter>
            <Button
              type="submit"
              color="primary"
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faSave} /> Save
            </Button>{" "}
            <Button
              color="danger"
              onClick={props.toggle}
              disabled={isProcessing}
              style={{ minWidth: "120px" }}
            >
              <FontAwesomeIcon icon={faWindowClose} /> Cancel
            </Button>
          </ModalFooter>
        </form>
      </Modal>
    </div>
  );
};

export default NewSchedules;
